import React from "react";

function alert() {
  return (
    <>
      <div className="alert alert-dark alert-dismissible fade show" role="alert">
         You should check in on some of those fields below.
        <button
          type="button"
          className="btn-close"
          data-bs-dismiss="alert"
          aria-label="Close"
        ></button>
      </div>
    </>
  );
}

export default alert;
